import React, { useState, useEffect } from "react";
import axios from "axios";
import toast from "react-hot-toast";
import { useNavigate, useParams } from "react-router-dom";
import { Box, Button, InputLabel, TextField, Typography } from "@mui/material";
import { styled } from "@mui/system";
import CustomButton from "../components/CustomButton.js";

const StyledTypography = styled(Typography)`
  font-family: Papyrus;
`;
const BlogDetails = () => {
  const [blog, setBlog] = useState({});
  const { id } = useParams();
  const navigate = useNavigate();
  const [inputs, setInputs] = useState({});

  // Get blog details
  const getBlogDetail = async () => {
    try {
      const { data } = await axios.get(`/api/v1/blog/get-blog/${id}`);
      if (data?.success) {
        setBlog(data?.blog);
        setInputs({
          title: data?.blog.title,
          description: data?.blog.description,
          image: data?.blog.image,
        });
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getBlogDetail();
  }, [id]);

  // Input change
  const handleChange = (e) => {
    setInputs((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
    }));
  };
  const handleBlog = () => {
    navigate("/blogs");
  };
  // Form submission
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.put(`/api/v1/blog/update-blog/${id}`, {
        title: inputs.title,
        description: inputs.description,
        image: inputs.image,
      });
      if (data?.success) {
        toast.success("Blog Updated");
        navigate("/my-blogs");
      }
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <Box
      display="flex"
      justifyContent="center"
      alignItems="center"
      height="100vh"
    >
      <Box width="60%" p={4} borderRadius={10} boxShadow={3} bgcolor="#f0f0f0">
        <StyledTypography variant="h5" fontWeight="bold" mb={3} color="#333">
          Update Your Memoir
        </StyledTypography>
        <form onSubmit={handleSubmit}>
          <InputLabel htmlFor="title" mb={2}>
            <StyledTypography>
              Title:
            </StyledTypography>
          </InputLabel>
          <TextField
            id="title"
            name="title"
            value={inputs.title || ""}
            onChange={handleChange}
            margin="dense"
            variant="outlined"
            fullWidth
            required
            InputProps={{
              style: { fontFamily: "Papyrus" },
            }}
          />
          <InputLabel htmlFor="description" mb={2} mt={3}>
            <StyledTypography>
              Description:
            </StyledTypography>
          </InputLabel>
          <TextField
            id="description"
            name="description"
            value={inputs.description || ""}
            onChange={handleChange}
            margin="dense"
            variant="outlined"
            fullWidth
            required
            multiline
            rows={4}
            InputProps={{
              style: { fontFamily: "Papyrus" },
            }}
          />
          <InputLabel htmlFor="image" mb={2} mt={3}>
            <StyledTypography>Image URL</StyledTypography>
          </InputLabel>
          <TextField
            id="image"
            name="image"
            value={inputs.image || ""}
            onChange={handleChange}
            margin="dense"
            variant="outlined"
            fullWidth
            required
            InputProps={{
              style: { fontFamily: "Papyrus" },
            }}
          />
          <CustomButton onClick={handleSubmit} >
            Update Memoir
          </CustomButton>
          
          <CustomButton onClick={handleBlog}>
            Back To Blogs
          </CustomButton>
        </form>
      </Box>
    </Box>
  );
};

export default BlogDetails;
