import React, { useState, useEffect } from "react";
import axios from "axios";
import { Box, Typography, CircularProgress, Button } from "@mui/material";
import BlogCard from "../components/BlogCard";
import "./USerBlog.css"; // Import the CSS file for styling
import { styled } from "@mui/system";
import { useNavigate } from "react-router-dom";
import CustomButton from "../components/CustomButton.js";

const StyledTypography = styled(Typography)`
  font-family: Papyrus;
`;
const Bloggers = () => {
  const [users, setUsers] = useState([]);
  const navigate = useNavigate();
  const [loading, setLoading] = useState(true);
  const getAllUsers = async () => {
    try {
      const { data } = await axios.get("/api/v1/user/all-users");
      if (data?.success) {
        setUsers(data?.users);
      }
      setLoading(false);
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };

  useEffect(() => {
    getAllUsers();
  }, []);

  const handleExploreBlogs = (userId) => {
    navigate(`/blogsofuser/${userId}`)
  };

  return (
    <>
      <center>
       <StyledTypography variant="h2" mb={3} style={{ fontFamily: "Papyrus", fontStyle: "italic", fontWeight: "bold"}}>
         Bloggers
      </StyledTypography>
    </center>
      {loading ? (
        <CircularProgress />
      ) : (
        <div>
          {users &&
            users.map((user, index) => (
              <Box
                key={user?._id}
                sx={{
                  border: "1px solid #ccc",
                  borderRadius: "10px",
                  padding: "1rem",
                  marginBottom: "1rem",
                  display: "flex",
                  flexDirection: "column",
                  alignItems: "center",
                }}
              >
                <StyledTypography variant="h6">
                  {index + 1}. Name : {user?.username}
                </StyledTypography>
                <StyledTypography>
                  Email  : {user?.email}
                </StyledTypography>
                <StyledTypography>
                  Number of Blogs : {user?.blogs.length}
                </StyledTypography>
                <CustomButton
                  onClick={() => handleExploreBlogs(user?._id)}
                >
                  Explore Blogs By {user?.username}
                </CustomButton>
              </Box>
            ))}
        </div>
      )}
    </>
  );
};

export default Bloggers;
