import axios from "axios";
import React from "react";
import { useNavigate } from "react-router-dom";
import { Card, CardHeader, CardMedia, CardContent, Avatar, Typography, IconButton, Box, Button, CircularProgress } from "@mui/material";
import { red } from "@mui/material/colors";
import ModeEditIcon from "@mui/icons-material/ModeEdit";
import DeleteIcon from "@mui/icons-material/Delete";
import toast from "react-hot-toast";
import { styled } from "@mui/system";
import CustomButton from "./CustomButton.js";

const BlogCard = ({
  title,
  description,
  image,
  username,
  time,
  id,
  isUser,
}) => {
  const navigate = useNavigate();

  const handleReadMore = () => {
    navigate(`/test/${id}`);
    toast.success("Read Complete Blog");
  };

  const handleEdit = () => {
    navigate(`/blog-details/${id}`);
  };

  const handleDelete = async () => {
    try {
      const { data } = await axios.delete(`/api/v1/blog/delete-blog/${id}`);
      if (data?.success) {

        toast.success("Blog Deleted Successfully !");
        // window.location.reload();
      }
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <Card
      sx={{
        width: "100%",
        margin: "auto",
        mt: 2,
        padding: 2,
        boxShadow: "5px 5px 10px #ccc",
        transition: "box-shadow 0.3s",
        ":hover": {
          boxShadow: "10px 10px 20px #ccc",
        },
        "@media (min-width: 768px)": {
          width: "calc(100% / 3 - 20px)", // Adjust the width to fit three cards in a row
          marginBottom: "20px",
        },
        "@media (max-width: 767px)": {
          width: "calc(50% - 20px)", // Adjust the width to fit two cards in a row
          marginBottom: "20px",
        },
        "@media (max-width: 500px)": {
          width: "100%", // Adjust the width to fit two cards in a row
          marginBottom: "20px",
        },
      }}
    >
      {isUser && (
        <Box display="flex">
          <IconButton onClick={handleEdit} sx={{ marginLeft: "auto" }}>
            <ModeEditIcon color="info" />
          </IconButton>
          <IconButton onClick={handleDelete}>
            <DeleteIcon color="error" />
          </IconButton>
        </Box>
      )}
      <CardHeader
        avatar={
          <Avatar sx={{ bgcolor: "black" }} aria-label="recipe">
            <Typography variant="subtitle1" color="white" style={{ fontFamily: "papyrus", fontStyle: "italic", fontWeight: "bold" }} >
              {username}
            </Typography>
          </Avatar>
        }
        title={
          <>
            <Typography variant="subtitle1" color="text.secondary" style={{ fontFamily: "robot", fontStyle: "italic", fontWeight: "bold" }} >
              Author: <span variant="subtitle1" color="text.secondary" style={{ fontFamily: "papyrus", fontStyle: "italic", fontWeight: "bold" }} >
                {username}
              </span>
            </Typography>
          </>
        }
        subheader={
          <Typography variant="caption" color="text.secondary" style={{ fontFamily: "robot", fontStyle: "italic", fontWeight: "bold" }}>
            On: {time}
          </Typography>
        }
      />
      <CardMedia component="img" height="194" image={image} alt="Not Available" />
      <CardContent>
        <Box maxHeight="50px" overflow="auto">
          <Typography variant="h6" color="text.secondary" style={{ fontFamily: "robot", fontStyle: "italic", fontWeight: "bold" }} >
            Title:<span style={{ fontFamily: "papyrus", fontStyle: "italic", fontWeight: "bold" }}>
              {title}
            </span>
          </Typography>
        </Box>
        <Box maxHeight="50px" overflow="auto">
          <Typography variant="body2" color="text.secondary" style={{ fontFamily: "robot", fontStyle: "italic", fontWeight: "bold" }}>
            Description: <span style={{ fontFamily: "papyrus", fontStyle: "italic", fontWeight: "bold" }}>
              {description}
            </span>
          </Typography>
        </Box>
      </CardContent>
      <Box display="flex" justifyContent="flex-end" mt={1}>
        <CustomButton onClick={handleReadMore}>
          Read More
        </CustomButton>
      </Box>
    </Card>
  );
};

export default BlogCard;
