import React from "react";
import { Button } from "@mui/material";

const CustomButton = ({ onClick, children }) => {
  return (
    <Button
      style={{ fontFamily: "papyrus", fontStyle: "italic", fontWeight: "bold" }}
      variant="outlined"
      onClick={onClick}
      sx={{
        textTransform: "none",
        border: "none",
        background: "transparent",
        color: "black",
        fontSize: "15px",
        "&:hover": {
          fontSize: "16px",
          border: "none",
          background: "transparent",
          color: "green",
        },
      }}
    >
      {children}
    </Button>
  );
};

export default CustomButton;
