import React, { useState, useEffect } from "react";
import axios from "axios";
import BlogCard from "../components/BlogCard";
import {
  Box,
  Typography,
  Link,
  Grid,
  TextField,
  Button,
  Container,
  CircularProgress
} from "@mui/material";
import FacebookIcon from "@mui/icons-material/Facebook";
import TwitterIcon from "@mui/icons-material/Twitter";
import InstagramIcon from "@mui/icons-material/Instagram";
import Logo from "./logo.png";
import styled, { keyframes } from "styled-components";

const fadeIn = keyframes`
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
`;
const StyledTypography = styled(Typography)`
  animation: ${fadeIn} 3s ease-in-out forwards;
`;
const Blogs = () => {
  const [blogs, setBlogs] = useState([]);

  const [loading, setLoading] = useState(true);
  const getAllBlogs = async () => {
    try {
      const { data } = await axios.get("/api/v1/blog/all-blog");
      if (data?.success) {
        setBlogs(data?.blogs);
      }
      setLoading(false);
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };

  useEffect(() => {
    getAllBlogs();
  }, []);
  return (
    <Box display="flex" flexDirection="column" alignItems="center" mt={5}>
    <StyledTypography variant="h2" mb={3} style={{ fontFamily: "papyrus", fontStyle: "italic", fontWeight: "bold"}}>
  Blogs
</StyledTypography>

      {loading ? (
        <CircularProgress />
      ) : (
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            flexWrap: "wrap",
            gap: "16px",
          }}
        >
          {blogs &&
            blogs.map((blog) => (
              <BlogCard
                key={blog?._id}
                id={blog?._id}
                isUser={localStorage.getItem("userId") === blog?.user?._id}
                title={blog?.title}
                description={blog?.description}
                image={blog?.image}
                username={blog?.user?.username}
                time={blog.createdAt}
              />
            ))}
        </div>
      )}
      <footer
  style={{
    background: "#fff",
    marginTop: "2rem",
    width: "100%",
  }}
>
  <Container maxWidth="lg">
    <Grid container spacing={2}>
      <Grid item xs={12} sm={6}>
        <Box>
          <Typography variant="h6" sx={{ marginBottom: "0.5rem" }} style={{ fontFamily: "papyrus", fontStyle: "italic", fontWeight: "bold"}}>
            Contact Us
          </Typography>

          <form action="mailto:adityalahane31@gmail.com" method="POST">
            <TextField
              label="Name"
              variant="outlined"
              fullWidth
              margin="dense"
              required
            />
            <TextField
              label="Email"
              variant="outlined"
              fullWidth
              margin="dense"
              required
            />
            <TextField
              label="Message"
              variant="outlined"
              fullWidth
              margin="dense"
              multiline
              rows={4}
              required
            />
            <Button
            variant="outlined"
            sx={{
              textTransform: "none",
              borderColor: "black",
              color: "black",
              fontSize: "15px",
              fontWeight: 600,
              "&:hover": {
                fontSize: "16px",
                border: "none",
                background: "transparent",
                color: "green",
              },
            }}
          >SEND
          </Button>
          </form>
        </Box>
      </Grid>

      <Grid item xs={12} sm={6} >
        <Box display="flex" flexDirection="column" alignItems="center">
          <Typography variant="h6" sx={{ marginBottom: "0.5rem" }} style={{ fontFamily: "papyrus", fontStyle: "italic", fontWeight: "bold"}}>
            Follow us on:
          </Typography>
          <Box display="flex" gap={2}>
            <Link href="https://twitter.com/Adi_Lahane_" color="inherit" underline="none">
              <TwitterIcon fontSize="large" />
            </Link>
            <Link href="https://www.instagram.com/adi_lahane_/" color="inherit" underline="none">
              <InstagramIcon fontSize="large" />
            </Link>
          </Box>

          <Box marginTop={2}>
            <img src={Logo} alt="App Logo" style={{ width: 40, height: 40 }} />
          </Box>
          <Button
          
            variant="outlined"
            sx={{
              fontFamily: "papyrus", 
              fontStyle: "italic",
              textTransform: "none",
              borderColor: "black",
              color: "black",
              fontSize: "15px",
              fontWeight: 600,
              "&:hover": {
                fontSize: "16px",
                border: "none",
                background: "transparent",
                color: "green",
              },
            }}
          >
            <Link href="https://newsletter-3-0.onrender.com/" color="inherit" underline="none" >
              Newsletter
            </Link>
          </Button>
        </Box>
      </Grid>
    </Grid>
  </Container>
</footer>


    </Box>
  );
};

export default Blogs;
