import React, { useState, useEffect } from "react";
import axios from "axios";
import { Box, Typography, CircularProgress } from "@mui/material";
import BlogCard from "../components/BlogCard";
import { styled } from "@mui/system";
import { useNavigate, useParams } from "react-router-dom";
import CustomButton from "../components/CustomButton.js";

const StyledTypography = styled(Typography)`
  font-family: Papyrus;
`;
const UserBlogs = () => {
  const [blogs, setBlogs] = useState([]);
  const [loading, setLoading] = useState(true);
  const { id } = useParams();
  const [userName, setUserName] = useState("");
    const navigate = useNavigate(); 
    const handleExploreBloggers = (userId) => {
      navigate(`/all-users`)
    };
  
  // Get user blogs
  const getUserBlogs = async () => {
    try {
      const { data } = await axios.get(`/api/v1/blog/user-blog/${id}`);
      if (data?.success) {
        setBlogs(data?.userBlog.blogs);
      }
      setLoading(false);
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };

  useEffect(() => {
    getUserBlogs();
  }, []);
  useEffect(() => {
    const storedUserId = id;
    if (storedUserId) {
      const fetchUserName = async () => {
        try {
          const { data } = await axios.get(`/api/v1/user/get-user/${storedUserId}`);
          const username = data?.user.username;
          setUserName(username);
        } catch (error) {
          console.error("Error fetching user data:", error);
        }
      };

      fetchUserName();
    }
  }, [id]);
  return (
    <Box display="flex" flexDirection="column" alignItems="center" mt={5}>
      <StyledTypography variant="h4" mb={3}>
        Blogs By {userName}
      </StyledTypography>
      <CustomButton
        onClick={handleExploreBloggers}
      >
        Go Back To Bloggers List 
      </CustomButton>
      {loading ? (
        <CircularProgress />
      ) : (
        <Box
          display="flex"
          justifyContent="center"
          flexWrap="wrap"
          gap="16px"
          maxWidth="1200px"
          width="100%"
          mt={2}
        >
          {blogs && blogs.length > 0 ? (
            blogs.map((blog) => (
              <BlogCard
                key={blog._id}
                id={blog._id}
                isUser={localStorage.getItem("userId") === blog?.user?._id}
                title={blog.title}
                description={blog.description}
                image={blog.image}
                username={blog.user.username}
                time={blog.createdAt}
              />
            ))
          ) : (
            <StyledTypography variant="h6">
              User doesn't have any blog !
            </StyledTypography>
          )}
        </Box>
      )}
    </Box>
  );
};

export default UserBlogs;
