import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import {
  Box,
  Tab,
  Tabs,
  AppBar,
  Toolbar,
  Button,
  Typography,
  useMediaQuery,
  useTheme,
  IconButton,
  Drawer,
  List,
  ListItem,
  ListItemText,
} from "@mui/material";
import { Link, useLocation } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { authActions } from "../redux/store";
import toast from "react-hot-toast";
import { styled } from "@mui/system";
import MenuIcon from "@mui/icons-material/Menu";

const StyledTypography = styled(Typography)`
  font-family: Papyrus;
`;

const Header = () => {
  const isLogin = useSelector((state) => state.isLogin);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [userName, setUserName] = useState("");
  const theme = useTheme();
  const isSmallScreen = useMediaQuery(theme.breakpoints.down("sm"));
  // const isSmallScreen = useMediaQuery("(max-width: 300px)");
  const [drawerOpen, setDrawerOpen] = useState(false);

  const handleLogout = () => {
    try {
      dispatch(authActions.logout());
      toast.success("Logout Successfully");
      navigate("/login");
      localStorage.clear();
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    const storedUserId = localStorage.getItem("userId");

    const fetchUserName = async () => {
      try {
        const response = await fetch(`/api/v1/user/get-user/${storedUserId}`);
        if (!response.ok) {
          throw new Error("Failed to fetch user data");
        }
        const data = await response.json();
        const username = data.user.username;
        setUserName(username);
      } catch (error) {
        console.error("Error fetching user data:", error);
      }
    };

    if (storedUserId) {
      fetchUserName();
    }
  }, []);
  useEffect(() => {
    // Check if the user ID is present in storage on page load
    const storedUserId = localStorage.getItem("userId");
    if (storedUserId) {
      dispatch(authActions.login()); // Dispatch the login action to update the login state
    }
  }, []);
  const CustomTab = ({ label, to }) => {
    const location = useLocation();
    const isActive = location.pathname === to;

    return (
      <Tab
        component={Link}
        to={to}
        disableRipple
        sx={{
          fontFamily: "Papyrus",
          fontWeight: 600,
          textTransform: "none",
          "&.Mui-selected": {
            color: "green",
          },
          "&:focus": {
            color: "green",
          },
          "&::after": {
            content: "''",
            position: "absolute",
            bottom: 0,
            left: 0,
            width: "100%",
            height: "4px",
            backgroundColor: isActive ? "black" : "transparent",
            transition: "background-color 0.3s",
          },
        }}
        label={<Typography variant="body1" style={{ fontFamily: "Papyrus" }}>{label}</Typography>}
      />
    );
  };

  const toggleDrawer = (open) => {
    setDrawerOpen(open);
  };
const handleBack =()=>{
  navigate(`/blogs`);
  window.location.reload();
}
  const drawerList = (
    <List sx={{ width: 250 }}>
      <ListItem button component={Link} to="/blogs">
        <ListItemText primary={<StyledTypography variant="body1" onClick={handleBack}>Back</StyledTypography>} />
      </ListItem>
      <ListItem button component={Link} to="/blogs">
        <ListItemText primary={<StyledTypography variant="body1">Blogs</StyledTypography>} />
      </ListItem>
      <ListItem button component={Link} to="/my-blogs">
        <ListItemText primary={<StyledTypography variant="body1">My Blogs</StyledTypography>} />
      </ListItem>
      <ListItem button component={Link} to="/create-blog">
        <ListItemText primary={<StyledTypography variant="body1">Create Blog</StyledTypography>} />
      </ListItem>
      <ListItem button component={Link} to="/all-users">
        <ListItemText primary={<StyledTypography variant="body1">Bloggers</StyledTypography>} />
      </ListItem>
    </List>
  );

  return (
    <>
      <AppBar position="sticky" sx={{ bgcolor: "#333" }}>
        <Toolbar>
          <Typography variant="h4" sx={{ flexGrow: 1, fontFamily: "Papyrus", fontWeight: "bold", textTransform: "none" }}>
             Memoir
          </Typography>
          {isLogin && (
            <>
              {isSmallScreen ? (
                <>
                  <IconButton edge="start" color="inherit" aria-label="menu" onClick={() => toggleDrawer(true)}>
                    <MenuIcon />
                  </IconButton>
                  <Drawer anchor="right" open={drawerOpen} onClose={() => toggleDrawer(false)}>
                    {drawerList}
                  </Drawer>
                </>
              ) : (
                <Box display="flex" marginRight="auto">
                  <Tabs textColor="inherit">
                    <CustomTab label="Blogs" to="/blogs" />
                    <CustomTab label="My Blogs" to="/my-blogs" />
                    <CustomTab label="Create Blog" to="/create-blog" />
                    <CustomTab label="Bloggers" to="/all-users" />
                  </Tabs>
                </Box>
              )}
            </>
          )}
          <Box display="flex" alignItems="center">
            {!isLogin ? (
              <>
                <Button sx={{ margin: 1, color: "white" }} component={Link} to="/blogs">
                  <StyledTypography variant="body1" textTransform={"none"}>Blogs</StyledTypography>
                </Button>
                <Button sx={{ margin: 1, color: "white" }} component={Link} to="/login">
                  <StyledTypography variant="body1" textTransform={"none"}>Login</StyledTypography>
                </Button>
                <Button sx={{ margin: 1, color: "white" }} component={Link} to="/register">
                  <StyledTypography variant="body1" textTransform={"none"}>Register</StyledTypography>
                </Button>
              </>
            ) : (
              <>
                <Typography
                  variant={isSmallScreen ? "subtitle1" : "body1"}
                  sx={{ color: "white", margin: "20px", textTransform: "none", fontFamily: "Papyrus" }}
                >
                  Hey! {userName}
                </Typography>
                <Button onClick={handleLogout} sx={{ margin: 1, color: "white" }}>
                  <StyledTypography variant="body1" textTransform={"none"}>Logout</StyledTypography>
                </Button>
              </>
            )}
          </Box>
        </Toolbar>
      </AppBar>
    </>
  );
};

export default Header;
