import React, { useState } from "react";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { Box, Button, InputLabel, TextField, Typography } from "@mui/material";
import toast from "react-hot-toast";
import CustomButton from "../components/CustomButton.js";

const CreateBlog = () => {
  const id = localStorage.getItem("userId");
  const navigate = useNavigate();
  const [inputs, setInputs] = useState({
    title: "",
    description: "",
    image: "",
  });

  // Input change
  const handleChange = (e) => {
    setInputs((prevState) => ({
      ...prevState,
      [e.target.name]: e.target.value,
    }));
  };
  const handleBlog = () => {
    navigate("/blogs");
  };
  // Form submission
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.post("/api/v1/blog/create-blog", {
        title: inputs.title,
        description: inputs.description,
        image: inputs.image,
        user: id,
      });
      if (data?.success) {
        toast.success("Blog Created");
        navigate("/my-blogs");
      }
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <Box
      display="flex"
      justifyContent="center"
      alignItems="center"
      height="100vh"
      style={{ fontFamily: "Papyrus" }}
    >
      <form>
        <Box
          width={500}
          padding={4}
          boxShadow={4}
          borderRadius={10}
          bgcolor="white"
        >
          <Typography
            variant="h4"
            fontWeight="bold"
            textAlign="center"
            mb={3}
            color="green"
            style={{ fontFamily: "Papyrus", fontStyle: "italic" }}
          >
            Create A Memoir
          </Typography>
          <InputLabel sx={{ fontSize: "18px", fontWeight: "bold", fontFamily: "Papyrus" }}>
            Title:
          </InputLabel>
          <TextField
            name="title"
            value={inputs.title}
            onChange={handleChange}
            fullWidth
            margin="normal"
            variant="outlined"
            required
            InputProps={{
              style: { fontFamily: "Papyrus" },
            }}
          />
          <InputLabel sx={{ fontSize: "18px", fontWeight: "bold", mt: 2, fontFamily: "Papyrus" }}>
            Description:
          </InputLabel>
          <TextField
            name="description"
            value={inputs.description}
            onChange={handleChange}
            fullWidth
            margin="normal"
            variant="outlined"
            required
            multiline
            rows={4}
            InputProps={{
              style: { fontFamily: "Papyrus" },
            }}
          />
          <InputLabel sx={{ fontSize: "18px", fontWeight: "bold", mt: 2, fontFamily: "Papyrus" }}>
            Image URL:
          </InputLabel>
          <TextField
            name="image"
            value={inputs.image}
            onChange={handleChange}
            fullWidth
            margin="normal"
            variant="outlined"
            required
            InputProps={{
              style: { fontFamily: "Papyrus" },
            }}
          />
          <CustomButton onClick={handleSubmit}>
            Submit!
          </CustomButton>
          <CustomButton onClick={handleBlog}>
            Back To Blogs
          </CustomButton>
        </Box>
      </form>
    </Box>
  );
};

export default CreateBlog;
