import React, { useState, useEffect } from "react";
import axios from "axios";
import { useNavigate, useParams } from "react-router-dom";
import { Box, Button, InputLabel, Typography, CardMedia, Grid } from "@mui/material";
import { styled } from "@mui/system";
import CustomButton from "../components/CustomButton.js";

const StyledTypography = styled(Typography)`
  font-family: Papyrus;
`;

const BlogDetailsPage = () => {
  const [blog, setBlog] = useState({});
  const { id } = useParams();
  const navigate = useNavigate();
  const [inputs, setInputs] = useState({});
  const [userName, setUserName] = useState("");

  // Get blog details
  const getBlogDetail = async () => {
    try {
      const { data } = await axios.get(`/api/v1/blog/get-blog/${id}`);
      if (data?.success) {
        setBlog(data?.blog);
        setInputs({
          title: data?.blog.title,
          description: data?.blog.description,
          image: data?.blog.image,
          userId:data?.blog.user
        });
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getBlogDetail();
    window.scrollTo(0, 0); // Scroll to the top of the page
  }, [id]);

  useEffect(() => {
    const storedUserId = inputs.userId;
    if (storedUserId) {
      const fetchUserName = async () => {
        try {
          const { data } = await axios.get(`/api/v1/user/get-user/${storedUserId}`);
          const username = data?.user.username;
          setUserName(username);
        } catch (error) {
          console.error("Error fetching user data:", error);
        }
      };
  
      fetchUserName();
    }
  }, [inputs.userId]);

  const handleHome = () => {
    navigate("/blogs");
  };

  return (
    <Box
      display="flex"
      justifyContent="center"
      alignItems="center"
      bgcolor="#f0f0f0"
    >
      <Box p={4} borderRadius={10} boxShadow={3} bgcolor="#fff" maxWidth={800}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <CardMedia
              component="img"
              height="300"
              image={inputs.image}
              alt="Not Available"
            />
          </Grid>
          <Grid item xs={12}>
            <StyledTypography variant="h5" component="h2" gutterBottom>
             Author: {userName}
            </StyledTypography>
          </Grid>
          <Grid item xs={12}>
            <StyledTypography variant="h5" component="h2" gutterBottom>
            Heading: {inputs.title}
            </StyledTypography>
          </Grid>
          <Grid item xs={12}>
            <StyledTypography variant="body1" component="p">
             Description: {inputs.description}
            </StyledTypography>
          </Grid>
          <Grid item xs={12}>
            <Box display="flex" justifyContent="flex-end" mt={3}>
              <CustomButton onClick={handleHome}>
              Back To Blogs
              </CustomButton>
            </Box>
          </Grid>
        </Grid>
      </Box>
    </Box>
  );
};

export default BlogDetailsPage;
